export const parseAttribute = (value) => {
    if (value && value.startsWith('[')) {
        return value.substring(value.indexOf(']') + 1).toLowerCase();
    }
    return null;
}
export const parseDataItemId = (value) => {
    // {DataItemBinding Id=e40c9441a39849ddaf24aff25d682ec8}
    if (value && value.includes('DataItemBinding')) {
        return value.substring(value.indexOf('=') + 1);
    }
    return null;
}
