import React, {Component} from 'react';
import FileUpload from '../FileUpload/FileUpload';
import convertToJsonAsync from '../../utils/fileProcess';
import FrontPanel from '../FrontPanel/FrontPanel';
import URLConnector from '../URLConnector/URLConnector';

import {controlPropertyMap} from '../../utils/controlMaps';
import {parseDataItemId} from '../../utils/parseUtils';

const getControlInfo = (frontPanel, controlName, controls) => {
    const controlValue = frontPanel[controlName];
    if (Array.isArray(controlValue)) {
        controlValue.forEach((value) => {
            controls.push({
                name: controlName,
                attrs: value.attributes,
            });
        });
    } else {
        controls.push({
            name: controlName,
            attrs: controlValue.attributes
        });
    }
}

class FrontPanelController extends Component {
    state = {
        frontPanel: null,
        controls: [],
        showFrontPanel: false,
        connectionState: 'Connect',
        dataItemValues: []
    }
    dataItemMap = new Map();

    handleConnected = (url) => {
        switch (this.state.connectionState) {
            case 'Connect':
                this.socket = new WebSocket(url);
                this.setState({connectionState: 'Connecting'});
                this.socket.onopen = () => {
                    this.setState({connectionState: 'Disconnect'});
                };
                this.socket.onmessage = (event) => {
                    // expect there is a message describing the map between dcoIndex and DataItem Id
                    // then we can use DataItem Id to get the control id or index to set the binding value
                    console.log(`{websocket message received: ${event}}`);
                }
                break;
            case 'Disconnect':
                this.socket.close();
                this.socket = null;
                this.setState({connectionState: 'Connect'});
                break;
            default:
                break;
        }
    }

    fileSelectedHandler = async(event) => {
        const jsonObj = await convertToJsonAsync(event.target.files);
        const frontPanel = jsonObj.SourceFile.VirtualInstrument.NativeFrontPanel;
        const controls = [];
        Object.keys(frontPanel)
            .filter((key) => key !== 'PanelExecutionWindow' && key !== 'attributes')
            .forEach((key) => {
                getControlInfo(frontPanel, key, controls);
            });
        this.setState({frontPanel, showFrontPanel: true, controls});
        controls.forEach((control, index) => {
            const controlInfo = controlPropertyMap[control.name];
            if (controlInfo.dataItemBindingNativeProp) {
                const dataItemId = parseDataItemId(control.attrs[controlInfo.dataItemBindingNativeProp]);
                dataItemId && this.dataItemMap.set(dataItemId, index);
            }
        });
        console.log(this.dataItemMap);
        console.log(controls);
    }

    render() {
        return (
            <>
            {!this.state.showFrontPanel && <FileUpload fileSelected={this.fileSelectedHandler}/>}
            {this.state.showFrontPanel && (
                <>
                <URLConnector connectionState={this.state.connectionState} onConnected={this.handleConnected} />
                <FrontPanel frontPanel={this.state.frontPanel} controls={this.state.controls} />
                </>
                )
            }
            </>
        )
    }
}
export default FrontPanelController;