import React, {useEffect, useRef} from 'react';
import labviewLogo from '../../assets/LVNXG.png';
import classes from './FileUpload.module.css';

const FileUpload = (props) => {
    const fileUploadEl = useRef(null);
    useEffect(() => {
        const element = fileUploadEl.current;
        element.addEventListener('change', props.fileSelected);
        return () => {
            element.removeEventListener('change', props.fileSelected);
        }
    }, [props.fileSelected]);
    return (
        <div className={classes.FileUpload}>
        <img src={labviewLogo} alt="LabVIEW NXG" className={classes.logo}></img>
        <input type="file" ref={fileUploadEl} className={classes.fileload}></input>
        </div>
    );
};

export default FileUpload;