import {parseAttribute} from './parseUtils';

export const controlPropertyMap = Object.freeze({
    Button: {
        tag: 'jqx-button',
        props: {
            Content: 'content',
            IsReadOnly: 'readonly',
        },
        dataItemBindingNativeProp : 'Value'
    },
    NumericText: {
        tag: 'jqx-numeric-text-box',
        props: {
            IsReadOnly: 'readonly'
        },
        dataItemBindingNativeProp : 'Value'
    },
    Slider: {
        tag: 'jqx-slider',
        props: {
            Orientation: 'orientation'
        },
        dataItemBindingNativeProp: 'Value'
    },
    Gauge: {
        tag: 'jqx-gauge',
        props: {
        },
        dataItemBindingNativeProp: 'Value'
    },
    Tank: {
        tag: 'jqx-tank',
        props: {
            Orientation: 'orientation'
        },
        dataItemBindingNativeProp: 'Value'
    },
    LVCheckBox: {
        tag: 'jqx-check-box',
        props: {
            Content: 'textContent'
        },
        dataItemBindingNativeProp: 'Value'
    },
    Switch: {
        tag: (attributes) => {
            const shape = parseAttribute(attributes.Shape);
            if (shape === 'power') {
                return 'jqx-power-button';
            }
            return 'jqx-switch-button';
        },
        props: {
            FalseContent: 'falseContent',
            TrueContent: 'trueContent',
            Orientation: 'orientation'
        },
        dataItemBindingNativeProp: 'Value'
    },
    LED: {
        tag: 'jqx-led',
        props: {
            FalseContent: 'falseContent',
            TrueContent: 'trueContent',
            Shape: 'shape',
        },
        dataItemBindingNativeProp: 'Value'
    },
    StringControl: {
        tag: 'jqx-text-box',
        props: {
        },
        dataItemBindingNativeProp: 'Text'
    },
    LVLinearProgressBar: {
        tag: 'jqx-progress-bar',
        props: {
            Maximum: 'max',
            Minimum: 'min',
            Orientation: 'orientation'
        },
        dataItemBindingNativeProp: 'Value'
    },
    LVRadialProgressBar: {
        tag: 'jqx-circular-progress-bar',
        props: {
            Maximum: 'max',
            Minimum: 'min',
        },
        dataItemBindingNativeProp: 'Value'
    },
    Label: {
        tag: 'div',
        props: {
            Text: 'textContent'
        }
    },
    // todo tlan: add more control keys here
});
