import React from 'react';
import {parseAttribute} from '../../utils/parseUtils';
import {controlPropertyMap} from '../../utils/controlMaps';

import '../../utils/importJqxElements';

const FrontPanel = ({frontPanel, controls}) => {
    const frontPanelAttrs = frontPanel.attributes;
    const height = parseAttribute(frontPanelAttrs.Height);
    return (
        <div style={{height: `${height}px`}}>
            {
                controls.map((control) => {
                    const controlInfo = controlPropertyMap[control.name];
                    let tagName;
                    if (typeof controlInfo.tag === 'function') {
                        tagName = controlInfo.tag(control.attrs);
                    } else {
                        tagName = controlInfo.tag;
                    }
                    const attrs = control.attrs;
                    const height = parseAttribute(attrs.Height);
                    const width = parseAttribute(attrs.Width);
                    const left = parseAttribute(attrs.Left);
                    const top = parseAttribute(attrs.Top);
                    const props = {
                        key: attrs.Id,
                    };
                    let textContent = null;
                    Object.keys(controlInfo.props).forEach((nativeProp) => {
                        const value = parseAttribute(attrs[nativeProp]);
                        const htmlAttrName = controlInfo.props[nativeProp];
                        if (htmlAttrName === 'textContent') {
                            textContent = value;
                        } else {
                            props[controlInfo.props[nativeProp]] = value;
                        }
                    });
                    props.style = {
                        height: height !== null ? `${height}px` : 'auto',
                        width: width !== null ? `${width}px` : 'auto',
                        left: `${left}px`,
                        top: `${top}px`,
                        position: 'absolute',
                        fontSize: textContent ? '12px' : 'inherit',
                    };
                    return React.createElement(tagName, props, textContent);
                })
            }
        </div>
    );
}
export default FrontPanel;
