import React, {useRef, useEffect} from 'react';
import classes from './URLConnector.module.css';

const URLConnector = (props) => {
    const { connectionState, onConnected } = props;
    const buttonEl = useRef(null);
    const textBoxEl = useRef(null);

    useEffect(() => {
        const button = buttonEl.current;
        const textBox = textBoxEl.current;
        const onButtonClick = () => {
            onConnected(textBox.value);
        };
        button.addEventListener('click', onButtonClick);
        return () => {
            button.removeEventListener('click', onButtonClick);
        }
    }, [onConnected]);

    return (
        <div className={classes.URLConnector}>
            <jqx-text-box placeholder="Input WebSocket URL here" ref={textBoxEl}></jqx-text-box>
            <jqx-button ref={buttonEl} content={connectionState}></jqx-button>
        </div>
    )
};
export default URLConnector;
